<template><undefined>
<h1>TimePicker 时间选择框</h1>
<p>当用户需要输入一个时间，可以点击标准输入框，弹出时间面板进行选择。</p>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;v-time-picker&gt;&lt;/v-time-picker&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
