</div>
                  <div slot="description"><h4 id="ji-ben"><a class="header-anchor" href="#ji-ben" aria-hidden="true">¶</a> 基本</h4>
<p>最简单的用法。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-time-picker</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-time-picker</span>&gt;</span>
</code></pre>
</div>
