  #### 基本
  最简单的用法。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-time-picker</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-time-picker</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;v-time-picker size=\&quot;large\&quot;&gt;&lt;/v-time-picker&gt;\n&lt;v-time-picker&gt;&lt;/v-time-picker&gt;\n&lt;v-time-picker size=\&quot;small\&quot;&gt;&lt;/v-time-picker&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><v-time-picker size="large"></v-time-picker>
<v-time-picker></v-time-picker>
<v-time-picker size="small"></v-time-picker>
</div>
                  <div slot="description"><h4 id="san-chong-da-xiao"><a class="header-anchor" href="#san-chong-da-xiao" aria-hidden="true">¶</a> 三种大小</h4>
<p>三种大小的输入框，大的用在表单中，中的为默认。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-time-picker</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"large"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-time-picker</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-time-picker</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-time-picker</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-time-picker</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-time-picker</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 三种大小
  三种大小的输入框，大的用在表单中，中的为默认。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-time-picker</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"large"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-time-picker</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-time-picker</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-time-picker</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-time-picker</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-time-picker</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;v-time-picker v-model=\&quot;value\&quot;&gt;&lt;/v-time-picker&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    data: ()=&gt; ({\n      value: '08:30:00'\n    }),\n    watch: {\n      value(val){\n        console.log(val)\n      }\n    }\n  }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
  <v-time-picker v-model="value"></v-time-picker>
</template>


</div>
                  <div slot="description"><h4 id="shou-kong-zu-jian"><a class="header-anchor" href="#shou-kong-zu-jian" aria-hidden="true">¶</a> 受控组件</h4>
<p>v-model 和 watch 需要配合使用。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-time-picker</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-time-picker</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span> ({
      <span class="hljs-attr">value</span>: <span class="hljs-string">'08:30:00'</span>
    }),
    <span class="hljs-attr">watch</span>: {
      value(val){
        <span class="hljs-built_in">console</span>.log(val)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 受控组件
  v-model 和 watch 需要配合使用。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-time-picker</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-time-picker</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span> ({
      <span class="hljs-attr">value</span>: <span class="hljs-string">'08:30:00'</span>
    }),
    <span class="hljs-attr">watch</span>: {
      value(val){
        <span class="hljs-built_in">console</span>.log(val)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;v-time-picker disabled&gt;&lt;/v-time-picker&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><v-time-picker disabled></v-time-picker>
</div>
                  <div slot="description"><h4 id="jin-yong"><a class="header-anchor" href="#jin-yong" aria-hidden="true">¶</a> 禁用</h4>
<p>禁用时间选择。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-time-picker</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-time-picker</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 禁用
  禁用时间选择。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-time-picker</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-time-picker</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;v-time-picker format=\&quot;HH:mm\&quot;&gt;&lt;/v-time-picker&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><v-time-picker format="HH:mm"></v-time-picker>
</div>
                  <div slot="description"><h4 id="xuan-ze-shi-fen"><a class="header-anchor" href="#xuan-ze-shi-fen" aria-hidden="true">¶</a> 选择时分</h4>
<p>TimePicker 浮层中的列会随着<code>format</code>变化，当略去<code>format</code>中的某部分时，浮层中对应的列也会消失。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-time-picker</span> <span class="hljs-attr">format</span>=<span class="hljs-string">"HH:mm"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-time-picker</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 选择时分
  TimePicker 浮层中的列会随着`format`变化，当略去`format`中的某部分时，浮层中对应的列也会消失。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-time-picker</span> <span class="hljs-attr">format</span>=<span class="hljs-string">"HH:mm"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-time-picker</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;v-time-picker :disabled-hours=\&quot;disabledHours\&quot; :disabled-minutes=\&quot;disabledMinutes\&quot; :disabled-seconds=\&quot;disabledSeconds\&quot;&gt;&lt;/v-time-picker&gt;\n  &lt;v-time-picker :disabled-hours=\&quot;disabledHours\&quot; :disabled-minutes=\&quot;disabledMinutes\&quot; :disabled-seconds=\&quot;disabledSeconds\&quot; hide-disabled-options&gt;&lt;/v-time-picker&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    methods: {\n        range(start, end) {\n            const result = [];\n            for (let i = start; i &lt; end; i++) {\n                result.push(i);\n            }\n            return result;\n        },\n        disabledHours(h) {\n            const hours = this.range(0, 60);\n            hours.splice(20, 4);\n            return hours.includes(h);\n        },\n        disabledMinutes(m) {\n            return this.range(0, 31).includes(m);\n        },\n        disabledSeconds(s) {\n            return this.range(30, 60).includes(s);\n        }\n    }\n  }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
  <v-time-picker :disabled-hours="disabledHours" :disabled-minutes="disabledMinutes" :disabled-seconds="disabledSeconds"></v-time-picker>
  <v-time-picker :disabled-hours="disabledHours" :disabled-minutes="disabledMinutes" :disabled-seconds="disabledSeconds" hide-disabled-options=""></v-time-picker>
</template>


</div>
                  <div slot="description"><h4 id="jin-zhi-xuan-xiang"><a class="header-anchor" href="#jin-zhi-xuan-xiang" aria-hidden="true">¶</a> 禁止选项</h4>
<p>可以使用 <code>disabledHours</code> <code>disabledMinutes</code> <code>disabledSeconds</code> 组合禁止用户选择某个时间，配合<code>hideDisabledOptions</code>可以直接把不可选择的项隐藏。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-time-picker</span> <span class="hljs-attr">:disabled-hours</span>=<span class="hljs-string">"disabledHours"</span> <span class="hljs-attr">:disabled-minutes</span>=<span class="hljs-string">"disabledMinutes"</span> <span class="hljs-attr">:disabled-seconds</span>=<span class="hljs-string">"disabledSeconds"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-time-picker</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-time-picker</span> <span class="hljs-attr">:disabled-hours</span>=<span class="hljs-string">"disabledHours"</span> <span class="hljs-attr">:disabled-minutes</span>=<span class="hljs-string">"disabledMinutes"</span> <span class="hljs-attr">:disabled-seconds</span>=<span class="hljs-string">"disabledSeconds"</span> <span class="hljs-attr">hide-disabled-options</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-time-picker</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
        range(start, end) {
            <span class="hljs-keyword">const</span> result = [];
            <span class="hljs-keyword">for</span> (<span class="hljs-keyword">let</span> i = start; i &lt; end; i++) {
                result.push(i);
            }
            <span class="hljs-keyword">return</span> result;
        },
        disabledHours(h) {
            <span class="hljs-keyword">const</span> hours = <span class="hljs-keyword">this</span>.range(<span class="hljs-number">0</span>, <span class="hljs-number">60</span>);
            hours.splice(<span class="hljs-number">20</span>, <span class="hljs-number">4</span>);
            <span class="hljs-keyword">return</span> hours.includes(h);
        },
        disabledMinutes(m) {
            <span class="hljs-keyword">return</span> <span class="hljs-keyword">this</span>.range(<span class="hljs-number">0</span>, <span class="hljs-number">31</span>).includes(m);
        },
        disabledSeconds(s) {
            <span class="hljs-keyword">return</span> <span class="hljs-keyword">this</span>.range(<span class="hljs-number">30</span>, <span class="hljs-number">60</span>).includes(s);
        }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 禁止选项
  可以使用 `disabledHours` `disabledMinutes` `disabledSeconds` 组合禁止用户选择某个时间，配合`hideDisabledOptions`可以直接把不可选择的项隐藏。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-time-picker</span> <span class="hljs-attr">:disabled-hours</span>=<span class="hljs-string">"disabledHours"</span> <span class="hljs-attr">:disabled-minutes</span>=<span class="hljs-string">"disabledMinutes"</span> <span class="hljs-attr">:disabled-seconds</span>=<span class="hljs-string">"disabledSeconds"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-time-picker</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-time-picker</span> <span class="hljs-attr">:disabled-hours</span>=<span class="hljs-string">"disabledHours"</span> <span class="hljs-attr">:disabled-minutes</span>=<span class="hljs-string">"disabledMinutes"</span> <span class="hljs-attr">:disabled-seconds</span>=<span class="hljs-string">"disabledSeconds"</span> <span class="hljs-attr">hide-disabled-options</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-time-picker</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
        range(start, end) {
            <span class="hljs-keyword">const</span> result = [];
            <span class="hljs-keyword">for</span> (<span class="hljs-keyword">let</span> i = start; i &lt; end; i++) {
                result.push(i);
            }
            <span class="hljs-keyword">return</span> result;
        },
        disabledHours(h) {
            <span class="hljs-keyword">const</span> hours = <span class="hljs-keyword">this</span>.range(<span class="hljs-number">0</span>, <span class="hljs-number">60</span>);
            hours.splice(<span class="hljs-number">20</span>, <span class="hljs-number">4</span>);
            <span class="hljs-keyword">return</span> hours.includes(h);
        },
        disabledMinutes(m) {
            <span class="hljs-keyword">return</span> <span class="hljs-keyword">this</span>.range(<span class="hljs-number">0</span>, <span class="hljs-number">31</span>).includes(m);
        },
        disabledSeconds(s) {
            <span class="hljs-keyword">return</span> <span class="hljs-keyword">this</span>.range(<span class="hljs-number">30</span>, <span class="hljs-number">60</span>).includes(s);
        }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;v-time-picker&gt;\n  &lt;template slot=\&quot;addon\&quot; slot-scope=\&quot;props\&quot;&gt;\n    &lt;v-button size=\&quot;small\&quot; type=\&quot;primary\&quot; @click=\&quot;props.panel.close()\&quot;&gt;OK&lt;/v-button&gt;\n  &lt;/template&gt;\n&lt;/v-time-picker&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><v-time-picker>
  <template slot="addon" slot-scope="props">
    <v-button size="small" type="primary" @click="props.panel.close()">OK</v-button>
  </template>
</v-time-picker>
</div>
                  <div slot="description"><h4 id="fu-jia-nei-rong"><a class="header-anchor" href="#fu-jia-nei-rong" aria-hidden="true">¶</a> 附加内容</h4>
<p>在 TimePicker 选择框底部显示自定义的内容。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-time-picker</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"addon"</span> <span class="hljs-attr">slot-scope</span>=<span class="hljs-string">"props"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-button</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"props.panel.close()"</span>&gt;</span>OK<span class="hljs-tag">&lt;/<span class="hljs-name">v-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">v-time-picker</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 附加内容
  在 TimePicker 选择框底部显示自定义的内容。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-time-picker</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"addon"</span> <span class="hljs-attr">slot-scope</span>=<span class="hljs-string">"props"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-button</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"props.panel.close()"</span>&gt;</span>OK<span class="hljs-tag">&lt;/<span class="hljs-name">v-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">v-time-picker</span>&gt;</span>
</code></pre>
</demo-box>
<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="timepicker-props"><a class="header-anchor" href="#timepicker-props" aria-hidden="true">¶</a> TimePicker Props</h3>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>默认时间</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>format</td>
<td>展示的时间格式</td>
<td>string</td>
<td>HH:mm:ss</td>
</tr>
<tr>
<td>position</td>
<td>下拉框的定位方式(absolute、fixed)</td>
<td>string</td>
<td>absolute</td>
</tr>
<tr>
<td>popupContainer</td>
<td>下拉菜单渲染父节点。默认渲染到 body 上，如果你遇到菜单滚动定位问题，试试修改为滚动的区域，并相对其定位。</td>
<td>function</td>
<td>() =&gt; document.body</td>
</tr>
<tr>
<td>size</td>
<td>控件大小（large、small）</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>禁用全部操作</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>disabledHours</td>
<td>禁止选择部分小时选项</td>
<td>function(hour)</td>
<td>-</td>
</tr>
<tr>
<td>disabledMinutes</td>
<td>禁止选择部分分钟选项</td>
<td>function(minite)</td>
<td>-</td>
</tr>
<tr>
<td>disabledSeconds</td>
<td>禁止选择部分秒选项</td>
<td>function(second)</td>
<td>-</td>
</tr>
<tr>
<td>hideDisabledOptions</td>
<td>隐藏禁止选择的选项</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>scopedSlot:addon</td>
<td>选择框底部显示自定义的内容</td>
<td>function</td>
<td>-</td>
</tr>
</tbody>
</table>
</undefined></template>

<script>
  export default {
    data: ()=> ({
      value: '08:30:00'
    }),
    watch: {
      value(val){
        console.log(val)
      }
    },
    methods: {
        range(start, end) {
            const result = [];
            for (let i = start; i < end; i++) {
                result.push(i);
            }
            return result;
        },
        disabledHours(h) {
            const hours = this.range(0, 60);
            hours.splice(20, 4);
            return hours.includes(h);
        },
        disabledMinutes(m) {
            return this.range(0, 31).includes(m);
        },
        disabledSeconds(s) {
            return this.range(30, 60).includes(s);
        }
    }
  }
</script>


